.header {
  padding: 40px 0;

  &__row {
    max-width: 1340px;
    padding: 0 15px;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
  }

  &__logo {
    display: inline-flex;
    flex-direction: column;
    align-items: center;

    p {
      font-weight: 300;
      font-size: 18px;
    }
  }
  &__btn {
    position: absolute;
    left: 5px;
    top: 0;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    cursor: pointer;
    z-index: 10;
    font-size: 14px;
    span {
      opacity: 0;
      @include m(768) {
        opacity: 1;
      }
    }

    @include m(1440) {
      font-size: 18px;
    }
    &:hover {
      color: $primary;
      transition: $hover-duration $timing;
      svg {
        stroke: $primary;
        transition: stroke $hover-duration $timing;
      }
    }
    svg {
      opacity: 1 !important;
      stroke: $black;
      margin-right: 15px;
    }
  }
}

.logo {
  max-width: 56px;
  margin-bottom: 6px;
}

.lang {
  position: absolute;
  right: 5px;
  top: 0;
  display: flex;

  &__link {
    font-size: 18px;
    position: relative;
    padding: 0 10px;
    font-weight: 300;

    &:not(:first-child) {
      &::before {
        content: '';
        width: 1px;
        height: 18px;
        background-color: $black;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(0, -50%);
      }
    }

    &.active {
      font-weight: 500;
    }
  }
}
